@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,700;0,900;1,400;1,700;1,900&display=swap);
body{margin:0;padding:0;font-family:"Rubik", sans-serif;background-color:#f2efef}.header{background:#00ff07;background:linear-gradient(90deg, #00ff07 26%, #00fff8 70%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#00ff07",endColorstr="#00fff8",GradientType=1);display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:5px}.header__icon{height:48px;width:48px}.header__title{font-weight:900;margin:15px}.loading{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:center;margin:50px auto;font-size:24px;font-weight:bold;position:relative}.loading:after{content:"";position:absolute;bottom:-20px;height:10px;width:25px;border-radius:5px;-webkit-animation-name:loading;animation-name:loading;-webkit-animation-duration:0.75s;animation-duration:0.75s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-timing-function:linear;animation-timing-function:linear}@-webkit-keyframes loading{0%{background-color:#00ff07;left:-25px}100%{background-color:#00fff8;left:100%}}@keyframes loading{0%{background-color:#00ff07;left:-25px}100%{background-color:#00fff8;left:100%}}.container{width:95%;margin:50px auto}@media (min-width: 1200px){.container{width:90%}}.single-post{border:1px solid #c1c1c1;border-radius:5px;box-sizing:border-box;padding:5px 15px;margin:25px 0;box-shadow:3px 3px 10px 0px rgba(18,18,18,0.5)}.single-post__comments-title,.search-box__label{position:relative}.single-post__comments-title:after,.search-box__label:after{content:"";width:100%;height:2.5px;position:absolute;bottom:-5px;left:0;margin:auto;background:#00ff07;background:linear-gradient(90deg, #00ff07 26%, #00fff8 70%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#00ff07",endColorstr="#00fff8",GradientType=1)}.single-post__comment{position:relative}.single-post__comment:after{content:"";width:100%;height:1px;position:absolute;bottom:-8px;left:0;margin:auto;background:#00ff07;background:linear-gradient(90deg, #00ff07 26%, #00fff8 70%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#00ff07",endColorstr="#00fff8",GradientType=1)}.single-post__comment-title{display:inline-block;font-weight:bold;margin:8px 0;font-size:14px}.single-post__comment-email{display:inline-block;font-size:12px;font-style:italic;margin:8px 0;margin-left:10px}.single-post__comment-body{font-size:14px;margin:4px 0 16px 0}.single-post__cta-box{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center}.cta{color:#f2efef;font-size:16px;border:none;padding:10px 20px;border-radius:3px;font-weight:bolder;margin:5px 0;transition:5s;background:#00ff07;background:linear-gradient(90deg, #00ff07 26%, #00fff8 70%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#00ff07",endColorstr="#00fff8",GradientType=1)}.cta:active,.cta:focus{outline:none}.cta__icon{position:relative;top:4px;right:-5px;transition:0.3s}.cta:hover{cursor:pointer}.cta:hover .cta__icon{right:-10px}.no-page{width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.no-page__link{color:#121212}.no-page__message,.no-results{text-align:center;font-size:60px}.search-box{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.search-box__label{font-weight:bold;margin-right:10px}.search-box__label:after{height:2.5px;bottom:-1.5px}.search-box__input{background-color:transparent;font-weight:bold;border:none;border-bottom:2.5px solid #121212;width:250px}.search-box__input:focus{outline:none}.search-results{font-weight:bold}

